<!-- 点击柱状图展示详情 -->
<template>
  <div class="show-detail-list" v-if="visible">
    <el-dialog
      class="dialog"
      title="提示"
      :visible.sync="visible"
      width="80%"
      :before-close="handleClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <span slot="title" class="dialog-header">
        <span class="header-text">{{ dialogTitle }}</span>
      </span>
      <div class="query-group" v-if="isQuery">
        <div class="search">
          <el-input style="width: 300px" v-model="queryForm.keyValue" size="mini" placeholder="请输入"></el-input>
          <el-button type="primary" size="mini" @click="query">查询</el-button>
        </div>
      </div>
      <bdaTable
        class="dialog-table"
        v-if="dialogRes"
        :tableData="dialogData"
        :headerColor="dialogHeaderColor"
        :isPage="true"
        :pageConfig="pageConfig"
        @changePage="changePage"
      ></bdaTable>

      <span slot="footer" class="dialog-footer">
        <el-button v-if="isExport" type="primary" @click="exportExcel" :loading="themeLoading">导出Excel</el-button>
        <el-button @click="handleClose">关闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
/**
 * @显示详情模态框
 * @author：risheng.huang
 * @ 参数说明：

 * */
// import Interface from '../base/mixin/talk-manage';
import bdaTable from '../bda-table/bda-table'

export default {
  components: {
    bdaTable
  },
  props: {
    visible: {
      type: Boolean,
      default: () => {
        return false;
      }
    },
    detailData: {
      type: Object,
      default: () => {
        return {

        }
      }
    },
    isQuery: {
      type: Boolean,
      default: () => {
        return false;
      }
    },
    isExport: {
      type: Boolean,
      default: () => {
        return true;
      }
    },
  },
  name: "show-detail-list",
  //   mixins: [Interface],
  data() {
    return {
      dialogVisible: false,
      dialogData: {},
      dialogRes: false,
      dialogHeaderColor: '',
      dialogTitle: "",
      queryForm: {
        keyValue: "",
        name: "",
        current: 1,
        size: 10,
        total: 0
      },
      //分页设置
      pageConfig: {
        name: "",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      themeLoading:false,
    }
  },
  mounted() {
    this.init()
  },
  watch: {
    detailData() {
      this.dataDeal(this.detailData)
    }
  },
  methods: {
    // 初始化数据
    init() {
      this.dataDeal(this.detailData)
    },
    //点击查询
    query(val) {
      this.$emit('changePageData', this.queryForm)
    },
    //改变分页
    changePage(data) {
      console.log(data, 'changePagedata')
      this.queryForm.name = data.name
      this.queryForm.size = data.size
      this.queryForm.current = data.current
      this.$emit('changePageData', this.queryForm)
    },
    //导出
    exportExcel() {
      this.themeLoading = true
      this.$emit('exportExcel', this.queryForm)
    },
    //关闭对话框
    handleClose() {
      // this.dialogRes = false;
      //   this.dialogVisible = false
      this.$emit("update:visible", false);
    },
    // 数据处理
    dataDeal(data) {
      if (Object.keys(data).length !== 0) {
        this.dialogRes = false
        this.$nextTick(() => {
          this.dialogData = this.detailData
          this.dialogHeaderColor = this.detailData.headerColor
          this.dialogTitle = this.detailData.title
          this.queryForm.name = this.detailData.name
          this.pageConfig.name = this.detailData.name
          this.pageConfig.size = this.detailData.size
          this.pageConfig.current = this.detailData.current
          this.pageConfig.total = this.detailData.total
          this.dialogRes = true
        })
      }
    },

  },
}
</script>

<style scoped lang="scss">
.show-detail-list {
  .dialog {
    ::v-deep .el-table .warning-row {
      background: #e9f3fe;
    }
    ::v-deep .el-dialog__header {
      padding: 10px 0 10px;
      border-bottom: 1px solid #e6e6e6;
    }
    ::v-deep .el-dialog__body {
      padding: 10px;
    }
    ::v-deep .el-dialog__footer {
      padding: 10px 10px 10px 0;
      border-top: 1px solid #e6e6e6;
      text-align: center;
    }
    .dialog-header {
      .header-text {
        margin-left: 20px;
        color: #808080;
        font-size: 14px;
      }
    }
    .query-group {
      margin-bottom: 10px;
      .search {
        display: flex;
        align-items: center;
      }
    }
    .dialog-table {
      height: 400px;
    }
    .dialog-pagination {
      text-align: center;
      margin: 10px 0;
    }
  }
}
</style>