<template>
  <div class="panoramicAnalysis">
    <div class="top">
      <div class="content">
        <div
          class="content-item"
          v-for="(item,index) in statusData"
          :key="index"
          :class="currentIndex===index?'active':''"
          @click="getDataByType(item.name,index)"
        >
          <div class="text">{{item.name}}</div>
          <div class="value" :style="{cursor: 'pointer'}">{{ item.value }}</div>
        </div>
      </div>
    </div>
    <div class="center">
      <div class="content content-border">
        <div class="content-title">
          <div class="content-title-left">
            <span class="block"></span>
            <span>停电中信息查询</span>
          </div>
          <div class="content-title-right">
            <div class="query">
              <span class="text">地市局：</span>
              <el-select
                class="input"
                v-model="municipalBureau"
                size="mini"
                clearable
                placeholder="请选择"
                @change="GetPowerAreaCode(1)"
              >
                <el-option
                  v-for="item in municipalBureauList"
                  :key="item.value"
                  :label="item.name"
                  :value="item.value"
                ></el-option>
              </el-select>
              <span class="text">县区局：</span>
              <el-select
                class="input"
                v-model="districtBureau"
                size="mini"
                clearable
                placeholder="请选择县区局"
                @change="GetPowerAreaCode(2)"
              >
                <el-option
                  v-for="item in districtBureauList"
                  :key="item.value"
                  :label="item.name"
                  :value="item.value"
                ></el-option>
              </el-select>
              <span class="text">供电所：</span>
              <el-select
                class="input"
                :disabled="orgLev>=4"
                v-model="powerSubstation"
                size="mini"
                clearable
                placeholder="请选择"
              >
                <el-option
                  v-for="item in powerSubstationList"
                  :key="item.value"
                  size="mini"
                  :label="item.name"
                  :value="item.value"
                ></el-option>
              </el-select>
            </div>
            <!-- v-if==false           暂时隐藏掉,用上面的地市接口-->
            <div class="query" v-if="false">
              <span class="text">地市局：</span>
              <el-select
                class="input"
                v-model="municipalBureau"
                size="mini"
                clearable
                placeholder="请选择"
                :disabled="orgLev>=2"
                @change="orgLev<2?getMunicipalBureauList(municipalBureau):()=>{}"
              >
                <el-option
                  v-for="item in municipalBureauList"
                  :key="item.value"
                  :label="item.name"
                  :value="item.value"
                ></el-option>
              </el-select>
              <span class="text">县区局：</span>
              <el-select
                class="input"
                v-model="districtBureau"
                size="mini"
                clearable
                placeholder="请选择"
                :disabled="orgLev>=3"
                @change="orgLev<3?getMunicipalBureauList(districtBureau):()=>{}"
              >
                <el-option
                  v-for="item in districtBureauList"
                  :key="item.value"
                  :label="item.name"
                  :value="item.value"
                ></el-option>
              </el-select>
              <span class="text">供电所：</span>
              <el-select
                class="input"
                :disabled="orgLev>=4"
                v-model="powerSubstation"
                size="mini"
                clearable
                placeholder="请选择"
              >
                <el-option
                  v-for="item in powerSubstationList"
                  :key="item.value"
                  size="mini"
                  :label="item.name"
                  :value="item.value"
                ></el-option>
              </el-select>
              <!--              <el-select-->
              <!--                v-model="outageInfoQueryForm.outageType"-->
              <!--                class="input"-->
              <!--                size="mini"-->
              <!--                clearable-->
              <!--                placeholder="请选择停电类型"-->
              <!--              >-->
              <!--                <el-option-->
              <!--                  v-for="item in outageTypeOptions"-->
              <!--                  :key="item.value"-->
              <!--                  :label="item.name"-->
              <!--                  :value="item.value"-->
              <!--                ></el-option>-->
              <!--              </el-select>-->
            </div>
            <div class="search">
              <el-input
                v-model="outageInfoQueryForm.searchText"
                class="search-box"
                size="mini"
                filterable
                placeholder="请输入用户编号或电话号码"
              ></el-input>
              <el-button type="primary" size="mini" @click="selectGetOutageInfoData">搜索</el-button>
            </div>
            <div class="history">
              <el-button type="primary" size="mini" @click="exportHistoryInfo" :loading="dfLoadig">导出当前页</el-button>
              <el-button type="primary" @click="checkHistory" size="mini">历史停电</el-button>
            </div>
          </div>
        </div>
        <div class="chart-main content-table">
          <bdaTable
            v-loading="tdLoading"
            class="main-table"
            :tableData="outageInfoData"
            :headerColor="headerColor"
            :isPage="true"
            :pageConfig="outageInfoPageConfig"
            @changePage="changePage"
          ></bdaTable>
        </div>
      </div>
    </div>
    <!-- <div class="bottom">
          <div class="content content-border">
            <div class="content-title">
              <div class="content-title-left">
                <span class="block"></span>
                <span>主题标签</span>
              </div>
            </div>
            <div class="chart-main">
              <themeLabelList
                :labelData="labelData"
                :pageConfig="labelPageConfig"
                @changePage="changePage"
                :showDetailData="labelDetailData"
                @clickDetail="getLabelDetailData"
                @changeDetailPage="changePage"
                @exportExcel="themeLabelExportExcel"
                :isQuery="true"
              ></themeLabelList>
            </div>
          </div>
    </div>-->
    <showDetailList
      v-if="showDetailVisible"
      :visible.sync="showDetailVisible"
      :detailData="countsDetailData"
      :isQuery="false"
      @changePageData="changePage"
      @exportExcel="detailListExportExcel"
    ></showDetailList>
    <el-dialog
      v-if="historyDetailvisible"
      class="dialog"
      title="提示"
      :visible.sync="historyDetailvisible"
      width="88%"
      :before-close="handleClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <span slot="title" class="dialog-header">
        <span class="header-button">{{ '停电客户明细列表' }}</span>
      </span>
      <div class="dialog-content">
        <div class="query">
          <el-input
            v-model="historyDetailForm.searchText"
            class="input"
            size="mini"
            filterable
            placeholder="请输入用户编号或电话号码"
          ></el-input>
          <el-date-picker
            v-model="historyDetailForm.searchTime"
            class="input"
            type="daterange"
            size="mini"
            value-format="yyyy-MM-dd"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
          ></el-date-picker>
          <el-select
            class="input"
            v-model="municipalBureau2"
            size="mini"
            clearable
            placeholder="请选择地市局"
            @change="GetPowerAreaCode2(1)"
          >
            <el-option
              v-for="item in municipalBureauList"
              :key="item.value"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-select
            class="input"
            v-model="districtBureau2"
            size="mini"
            clearable
            placeholder="请选择县区局"
            @change="GetPowerAreaCode2(2)"
          >
            <el-option
              v-for="item in districtBureauList"
              :key="item.value"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-select
            class="input"
            :disabled="orgLev>=4"
            v-model="powerSubstation2"
            size="mini"
            clearable
            placeholder="请选择供电所"
          >
            <el-option
              v-for="item in powerSubstationList"
              :key="item.value"
              size="mini"
              :label="item.name"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-button type="primary" size="mini" @click="getHistoryDetailData">查询</el-button>
        </div>
        <bdaTable
          v-loading="tableLoading"
          class="dialog-table"
          :tableData="historyDetailData"
          :headerColor="'#1899fa'"
          :isPage="true"
          :pageConfig="historyDetailPageConfig"
          @changePage="changePage"
        ></bdaTable>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="detailListExportExcel">导出Excel</el-button>
        <el-button @click="handleClose">关闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Interface from '@/base/mixin/outage-sensitive';
import Interface2 from '@/base/mixin/electricity-behavior';
import bdaTable from '@/components/bda-table/bda-table'
// import themeLabelList from '@/components/theme-label-list/theme-label-list'
import showDetailList from '@/components/show-detail-list/show-detail-list'
import { exportExcelGet } from "@/utils/export";
import { CP_PATH } from '@/config'
import axios from "axios";

export default {
  components: {
    bdaTable,
    // themeLabelList,
    showDetailList
  },
  name: "panoramicAnalysis",
  mixins: [Interface, Interface2],
  data() {
    return {
      //当前等级
      orgLev: '',
      //地市局
      municipalBureau: '',
      municipalBureauList: [],
      //县区局
      districtBureau: '',
      districtBureauList: [],
      //供电所
      powerSubstation: '',
      powerSubstationList: [],
      //供电类型选项
      outageTypeOptions: [],
      //各种停电状态信息与统计
      dfLoadig:false,
      statusData: [
        { name: '当前计划停电中', type: '', value: 0 },
        { name: '当前临时停电中', type: '', value: 0 },
        { name: '当前故障停电中', type: '', value: 0 },
        { name: '当前欠费停电中', type: '', value: 0 }],
      //台区详情
      countsDetailData: {},
      countsDetailQueryForm: {
        menuType: '9',
        tagName: "",
        current: 1,
        size: 10,
      },
      //分页设置
      countsDetailPageConfig: {
        name: " counts",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //停电中信息查询
      tdLoading: false,
      outageInfoData: {},
      currentIndex: 0,
      outageInfoQueryForm: {
        municipalBureau: "",
        outageType: "",
        searchText: "",
        current: 1,
        size: 10,
        total: 0
      },
      //分页设置
      outageInfoPageConfig: {
        name: "outageInfo",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //表头颜色
      headerColor: '',
      //关注标签
      labelData: [],

      labelQueryForm: {
        menuType: '9',
        current: 1,
        size: 10,
      },
      //分页设置
      labelPageConfig: {
        name: "label",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //标签详情
      labelDetailData: {},
      labelDetailQueryForm: {
        dbcolumnAS: "",
        menuType: '9',
        tagName: "",
        current: 1,
        size: 10,
      },
      //分页设置
      labelDetailPageConfig: {
        name: "labelDetail",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //明细表
      showDetailVisible: false,
      //历史详情
      historyDetailvisible: false,
      historyDetailData: {},
      historyDetailForm: {
        searchText: '',
        searchTime: [],
        powerSubstation: '',
        current: 1,
        size: 10,
      },
      historyPowerSubstationList: [],
      //分页设置
      historyDetailPageConfig: {
        name: 'historyDetail',
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },

      //历史停电
      municipalBureau2: '',//地市局
      districtBureau2: '',//县区局
      powerSubstation2: '',//供电所
      tableLoading: false,//地市局

    }
  },
  mounted() {
    this.getStatusData()
    this.getOutageInfoData()
    // this.getMunicipalBureauOptions()
    // this.getMunicipalBureauList()
    this.GetPowerAreaCode()
    this.getOutageTypeOptions()
    this.getLabelData()
  },
  methods: {
    //改变分页
    changePage(data) {
      console.log(data, 'data')
      if (data.name === 'counts') {
        this.countsDetailQueryForm.size = data.size
        this.countsDetailQueryForm.current = data.current
        this.getCountsDetailData()
      }
      if (data.name === 'label') {
        this.labelQueryForm.size = data.size
        this.labelQueryForm.current = data.current
        this.getLabelData()
      }
      if (data.name === 'labelDetail') {
        this.labelDetailQueryForm.dbcolumnAS = data.keyValue || ''
        this.labelDetailQueryForm.size = data.size
        this.labelDetailQueryForm.current = data.current
        this.getLabelDetailData()
      }
      if (data.name === 'outageInfo') {
        this.outageInfoQueryForm.size = data.size
        this.outageInfoQueryForm.current = data.current
        this.getOutageInfoData()
      }
      if (data.name === 'historyDetail') {
        this.historyDetailForm.size = data.size
        this.historyDetailForm.current = data.current
        this.getHistoryDetailData()
      }
    },
    //导出停电信息
    params(obj) {
      let result = '';
      let item;
      for (item in obj) {
        if (obj[item] && String(obj[item])) {
          result += `&${item}=${obj[item]}`;
        }
      }
      if (result) {
        result = '?' + result.slice(1);
      }
      return result;
    },
    exportHistoryInfo() {
      // const obj = {
      //   num: this.outageInfoQueryForm.searchText,
      //   dq: this.municipalBureau,
      //   gds: this.powerSubstation,
      //   xqj: this.districtBureau,
      // }
      // const url = `${CP_PATH}/timePowerFailureStatusMonitorNewController/exportTimePowerFailureInfo${this.params(obj)}`;
      this.dfLoadig = true
      const obj = {
        keyword: this.outageInfoQueryForm.searchText,
        dsj: this.municipalBureau,
        gds: this.powerSubstation,
        xqj: this.districtBureau,
        size: this.outageInfoQueryForm.size,
        current: this.outageInfoQueryForm.current,
      }
      // const url = `${CP_PATH}/customerOutageDetail/exportOutageInfo${this.params(obj)}`;
      // window.location.href = url
      let path = '/customerOutageDetail/exportOutageInfo'
      axios({
        method: "get",
        url: CP_PATH + path,
        params:obj,
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        },
        responseType: 'blob'
      }).then(res => {
        const content = res.data
        const blob = new Blob([content])
        const fileName = '导出信息.xls'
        if ('download' in document.createElement('a')) { // 非IE下载
          const elink = document.createElement('a')
          elink.download = fileName
          elink.style.display = 'none'
          elink.href = URL.createObjectURL(blob)
          document.body.appendChild(elink)
          elink.click()
          URL.revokeObjectURL(elink.href) // 释放URL 对象
          document.body.removeChild(elink)
        } else { // IE10+下载
          navigator.msSaveBlob(blob, fileName)
        }
      }).finally(()=>{
        this.dfLoadig = false
      }).catch(()=>{
        this.dfLoadig = false
      })
    },
    //
    checkHistory() {
      this.historyDetailvisible = true
      this.getHistoryDetailData()

    },
    //关闭坐席详情
    handleClose() {
      this.historyDetailForm.current = 1
      this.historyDetailForm.size = 10
      this.historyDetailvisible = false
      this.historyDetailForm.searchTime = []
      this.historyDetailForm.searchText = ''
      this.municipalBureau2 = '';
      this.districtBureau2 = '';
      this.powerSubstation2 = '';
    },
    //客户明细列表导出
    detailListExportExcel() {
      const obj = {
        num: this.historyDetailForm.searchText,
        startTime: this.historyDetailForm.searchTime[0],
        endTime: this.historyDetailForm.searchTime[1],
        dsj: this.municipalBureau2,
        xqj: this.districtBureau2,
        gds: this.powerSubstation2,
      }
      const url = `${CP_PATH}/timePowerFailureStatusMonitorNewController/exportHistoricalBlackout${this.params(obj)}`;
      window.location.href = url
    },
    //获取历史详情
    getHistoryDetailData() {
      this.tableLoading = true;
      this.historyDetailData = {}
      this.getOutageLisInfo({
        keyword: this.historyDetailForm.searchText,
        startTime: this.historyDetailForm.searchTime[0],
        endTime: this.historyDetailForm.searchTime[1],
        dsj: this.municipalBureau2,
        xqj: this.districtBureau2,
        gds: this.powerSubstation2,
        current: this.historyDetailForm.current,
        size: this.historyDetailForm.size,
      }).then(res => {
        this.historyDetailPageConfig.total = res.total
        this.historyDetailPageConfig.size = this.historyDetailForm.size
        this.historyDetailPageConfig.current = this.historyDetailForm.current
        this.historyDetailData = {
          tableData: [
            ...res.records
          ],
          columnNameData: [
            { prop: "czsj", label: "操作时间", width: '160' },
            { prop: "ldhm", label: "来电号码" },
            { prop: "yhbh", label: "客户编号" },
            { prop: "tdlx", label: "停电类型" },
            { prop: "jlzzzt", label: "计量装置状态" },
            { prop: "jlzzztcjsj", label: "计量装置状态采集时间", width: '160' },
            { prop: "tdsjsjkssj", label: "实际停电开始时间", width: '160' },
            { prop: "dsj", label: "供电单位" },
            { prop: "xlxd", label: "线路线段" },
            { prop: "tq", label: "用电台区" },
            { prop: "lxhm", label: "联系号码  " },
          ]
        }
        this.tableLoading = false;
        this.historyDetailvisible = true
      }).catch(err => {
        this.tableLoading = false;
        window.console.log(err)
      })
    },
    //主题标签导出
    themeLabelExportExcel(data) {
      exportExcelGet("/leanSeatingSkillController/exportLabelUserDisplay" + "?tagId=" + data.tagId);
    },
    // 各种停电状态信息与统计
    getStatusData() {
      this.getAllStatusTotal().then(res => {
        console.log(res, 'timePowerFailureStatusMonitorStatistics')
        this.statusData[0].value = res['当前计划停电中'] || 0
        this.statusData[1].value = res['当前临时停电中'] || 0
        this.statusData[2].value = res['当前故障停电中'] || 0
        this.statusData[3].value = res['当前欠费停电中'] || 0
        // this.statusData[0].value = res['all'] || 0
        // this.statusData[3].value = res['tq'] || 0
        // this.statusData[1].value = res['lx'][0] ? res['lx'][0].ACCOUNT || 0 : 0
        // this.statusData[2].value = res['lx'][1] ? res['lx'][1].ACCOUNT || 0 : 0客户日电量分析与监测
      })
    },
    //获取台区明细数据
    getCountsDetailData(data) {
      if (data) {
        this.countsDetailQueryForm.size = 10
        this.countsDetailQueryForm.current = 1
      }
      let columnNameData = [
        { prop: 'tqbs', label: '台区标识' },
        { prop: 'tqmc', label: '台区名称' },
        { prop: 'tdlx', label: '停电类型' },
        { prop: 'tdsb', label: '停电设备' },
        { prop: 'tdyy', label: '停电原因' },
        { prop: 'tdsj', label: '停电时间', },
        { prop: 'jhfdsj', label: '计划复电时间' },
        { prop: 'gdj', label: '供电局', },

      ]
      this.blackoutArea({ ...this.countsDetailQueryForm }).then(res => {
        this.countsDetailPageConfig.total = res.total
        this.countsDetailPageConfig.size = this.countsDetailQueryForm.size
        this.countsDetailPageConfig.current = this.countsDetailQueryForm.current
        this.countsDetailData = {
          tableData: [
            ...res.list
          ],
          headerColor: '#1899fa',
          title: "停电台区明细列表",
          name: 'counts',
          size: this.countsDetailQueryForm.size,
          current: this.countsDetailQueryForm.current,
          total: this.countsDetailPageConfig.total,
          columnNameData: columnNameData
        }
        this.showDetailVisible = true
      }).catch(err => {
        if (err) {
          this.countsDetailData = {
            tableData: [],
            columnNameData: columnNameData
          }
          this.showDetailVisible = true
        }
      })
    },
    // 根据类型查询
    getDataByType(type, index) {
      this.currentIndex = index
      this.outageInfoQueryForm.municipalBureau = ""
      this.outageInfoQueryForm.outageType = type
      this.outageInfoQueryForm.searchText = ""
      this.outageInfoQueryForm.current = 1
      this.outageInfoQueryForm.size = 10
      this.outageInfoQueryForm.total = 0
      this.outageInfoData.tableData = []
      //清空其他查询条件
      this.powerSubstation = ''
      this.districtBureau = ''
      this.municipalBureau = ''
      this.getOutageInfoData()
    },
    selectGetOutageInfoData(){
      this.outageInfoQueryForm.outageType = ''
      this.getOutageInfoData()
    },
    // 获取停电中信息查询
    getOutageInfoData() {
      // this.headerColor = '#1eb1b0'
      this.tdLoading = true
      this.headerColor = '#1899fa'
      let columnNameData = [
        { prop: "yhbh", label: "客户编号" },
        { prop: "lxdh", label: "联系电话" },
        { prop: "tdlx", label: "停电类型" },
        { prop: "jlzzzt", label: "计量装置状态" },
        { prop: "jlzzztcjsj", label: "计量装置状态采集时间" },
        { prop: "tdsjsjkssj", label: "实际停电开始时间" },
        { prop: "sjcszt", label: "三级催收状态" },
        { prop: "sjcsczsj", label: "三级催收状态操作时间" },
        { prop: "dsj", label: "地市局" },
        { prop: "xqj", label: "县区局" },
        { prop: "gds", label: "供电所" },
        // { prop: "qftdzt", label: "欠费停电状态" },
        // { prop: "qftdztczsj", label: "欠费停电状态操作时间" },
        // { prop: "gdj", label: "供电局" },
      ]
      this.getOutageInfo(
        {
          keyword: this.outageInfoQueryForm.searchText,
          tdlx: this.outageInfoQueryForm.outageType,
          dsj: this.municipalBureau,
          gds: this.powerSubstation,
          xqj: this.districtBureau,
          size: this.outageInfoQueryForm.size,
          current: this.outageInfoQueryForm.current,
        }
      ).then(res => {
        console.log(res, 'getTimePowerFailureInfo')
        this.tdLoading = false
        this.outageInfoPageConfig.total = res.total
        this.outageInfoPageConfig.size = this.outageInfoQueryForm.size
        this.outageInfoPageConfig.current = this.outageInfoQueryForm.current
        this.outageInfoData =
        {
          tableData: [...res.records],
          columnNameData: columnNameData
        }
      }
      ).catch(err => {
        if (err) {
          this.outageInfoData =
          {
            tableData: [],
            columnNameData: columnNameData
          }
        }
      })

    },
    // 获取关注标签
    getLabelData() {

      this.themeLabelDisplay(this.labelQueryForm).then(res => {
        this.labelPageConfig.total = res.total
        this.labelPageConfig.size = this.labelQueryForm.size
        this.labelPageConfig.current = this.labelQueryForm.current
        this.labelData = res.list.map(e => {
          return {
            ...e,
            name: e.tagName,
            text: e.tagDescription,
            value: e.account || 0,
          }
        })

        console.log(this.labelData, 'labelData')

      })
    },
    //获取标签详情信息
    getLabelDetailData(data) {
      if (data) {
        this.labelDetailQueryForm.tagId = data.tagId
        this.labelDetailQueryForm.size = 10
        this.labelDetailQueryForm.current = 1
      }
      let columnNameData = []
      this.labelUserDisplay({ ...this.labelDetailQueryForm }).then(res => {
        this.labelDetailPageConfig.total = res.result[0] ? res.result[0].total : 0
        this.labelDetailPageConfig.size = this.labelDetailQueryForm.size
        this.labelDetailPageConfig.current = this.labelDetailQueryForm.current
        if (res.showField[this.labelDetailQueryForm.tagId]) {
          columnNameData = res.showField[this.labelDetailQueryForm.tagId].map(e => {
            return {
              prop: e.dbcolumnAS,
              label: e.dbcolumnTitle,
            }
          }).filter(e => e.label !== '地区编码' && e.label !== '县区局编码' && e.label !== '供电单位编码' && e.label !== '供电电网编码')
        }
        console.log(columnNameData, 'columnNameData')
        res.result.shift()
        this.labelDetailData = {
          tableData: [...res.result],
          columnNameData: columnNameData,
          name: "labelDetail",
          headerColor: '#1899fa',
          title: "客户明细列表",
          total: this.labelDetailPageConfig.total,
          current: this.labelDetailQueryForm.current,
          size: this.labelDetailQueryForm.size,

        }
        // console.log(this.labelDetailData, 'labelDetailData')

      })
    },
    // 获取地市局数据
    getMunicipalBureauOptions() {
      this.powerSupplyBureau({}).then(res => {
        console.log(res, 'powerSupplyBureau')
        this.municipalBureauOptions = res.gdj.map(e => {
          return {
            name: e.orgName,
            value: e.orgName,
          }
        })
      })
    },
    // 获取停电类型数据
    getOutageTypeOptions() {
      this.powerOutageType({}).then(res => {
        console.log(res, 'powerOutageType')
        this.outageTypeOptions = res.map(e => {
          return {
            name: e.TDLX,
            value: e.value,
          }
        })
      })
    },
    // 获取地市局数据
    //获取供电单位编码
    GetPowerAreaCode(type = 0) {
      if (type === 1) {//下拉地市局
        this.districtBureau = ''
        this.powerSubstation = ''
        this.getPowerAreaCode({
          "orgLev": "3",//2==供电单位==供电单位，3==县区局局，4===供电所
          "orgCode": this.municipalBureau //上一级编码
        }).then(res => {
          this.districtBureauList = res.map(v => {
            return {
              value: v.ORG_CODE,
              name: v.ORG_NAME,
            }
          })
        })
      } else if (type === 2) {
        this.powerSubstation = ''
        this.getPowerAreaCode({
          "orgLev": "4",//2==供电单位==供电单位，3==县区局局，4===供电所
          "orgCode": this.districtBureau //上一级编码
        }).then(res => {
          this.powerSubstationList = res.map(v => {
            return {
              value: v.ORG_CODE,
              name: v.ORG_NAME,
            }
          })
        })
      } else {
        this.getPowerAreaCode({
          "orgLev": "2",//2==供电单位==供电单位，3==县区局局，4===供电所
          // "orgCode":"0306" //上一级编码
        }).then(res => {
          this.municipalBureauList = res.map(v => {
            return {
              value: v.ORG_CODE,
              name: v.ORG_NAME,
            }
          })
          window.console.log(this.municipalBureauList, 'ttttttssssssssssssss')
        })
      }
    },
    // 历史停电获取地市局数据
    // 历史停电获取供电单位编码
    GetPowerAreaCode2(type = 0) {
      if (type === 1) {//下拉地市局
        this.districtBureau2 = ''
        this.powerSubstation2 = ''
        this.getPowerAreaCode({
          "orgLev": "3",//2==供电单位==供电单位，3==县区局局，4===供电所
          "orgCode": this.municipalBureau2 //上一级编码
        }).then(res => {
          this.districtBureauList = res.map(v => {
            return {
              value: v.ORG_CODE,
              name: v.ORG_NAME,
            }
          })
        })
      } else if (type === 2) {
        this.powerSubstation2 = ''
        this.getPowerAreaCode({
          "orgLev": "4",//2==供电单位==供电单位，3==县区局局，4===供电所
          "orgCode": this.districtBureau2 //上一级编码
        }).then(res => {
          this.powerSubstationList = res.map(v => {
            return {
              value: v.ORG_CODE,
              name: v.ORG_NAME,
            }
          })
        })
      } else {
        this.getPowerAreaCode({
          "orgLev": "2",//2==供电单位==供电单位，3==县区局局，4===供电所
          // "orgCode":"0306" //上一级编码
        }).then(res => {
          this.municipalBureauList = res.map(v => {
            return {
              value: v.ORG_CODE,
              name: v.ORG_NAME,
            }
          })
          window.console.log(this.municipalBureauList, 'ttttttssssssssssssss')
        })
      }
    },
    getMunicipalBureauList(val) {
      console.log(val, 'valeeeeeeeeeeeee')
      this.getPowerSupplyBureau(val ? { powerSupplyCode: val } : {}).then(res => {
        console.log(res)
        this.orgLev = this.orgLev || this.orgLev === 0 ? this.orgLev : res.orgLev
        console.log(this.orgLev, 'orgLev')

        if (res.orgLev <= 1) {
          this.municipalBureauList = res.downFrameLevel.map(e => {
            return {
              name: e.orgName,
              value: e.orgId,
            }
          })
        }
        if (res.orgLev === 2) {
          this.municipalBureau = this.municipalBureau ? this.municipalBureau : res.city
          this.districtBureauList = res.downFrameLevel.map(e => {
            return {
              name: e.orgName,
              value: e.orgId,
            }
          })

        }
        if (res.orgLev === 3) {
          this.municipalBureau = this.municipalBureau ? this.municipalBureau : res.city
          this.districtBureau = this.districtBureau ? this.districtBureau : res.county
          this.powerSubstationList = res.downFrameLevel.map(e => {
            return {
              name: e.orgName,
              value: e.orgId,
            }
          })
        }
        if (res.orgLev === 4) {
          this.municipalBureau = this.municipalBureau ? this.municipalBureau : res.city
          this.districtBureau = this.districtBureau ? this.districtBureau : res.county
          this.powerSubstationList = []
          this.powerSubstationList.push({
            name: res.courts,
            value: res.powerSupplyCode,
          })
          this.powerSubstation = this.powerSubstation ? this.powerSubstation : res.powerSupplyCode

        }

      })


    },
  },
}
</script>

